import Vue from 'vue'
import VueRouter from 'vue-router'

import home from '@/components/home/home'
import home1 from '@/components/home1/home1'

Vue.use(VueRouter)

const routes =[{
    name:'home',
    path:'/',
    component:home

},{
    name:'home1',
    path:'/home1',
    component:home1

}]

export default new VueRouter({
    mode:'history',
    routes
})